import { createTheme } from '@mui/material/styles';
const theme = createTheme({
  palette: {
  	background: {
      default: "#FFFCF2"
    },
    primary: {
      main: "#252422",
    },
    secondary: {
      main: "#403D39",
    },
  },
  typography: {
    fontFamily: [
      'Roboto Slab',
    ].join(','),
    fontSize: 30,
    button: {
      fontFamily: ['Roboto Slab'].join(','),
      fontSize: 16,
      fontWeight: 400,
    },

  }
})
export default theme