import React from 'react';
import { makeStyles } from '@mui/styles';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import ButtonGroup from '@mui/material/ButtonGroup';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';

const useStyles = makeStyles((theme) => ({
appbar: {
     background : 'black'
},
button: {
     display: 'flex',
     flexDirection: 'column',
     alignItems: 'center',
                '& > *': {
                  m: 1,
                },

},
}));

function Feed(){
    const classes = useStyles()
    return (        
        <>
        <div style={{display: 'flex', alignItems:'center'}}>
        <Box
          m="auto"  
          component="img"
          sx={{
            maxHeight: { xs: 233, md: 167 },
            maxWidth: { xs: 350, md: 250 },
            borderRadius: 5,
            padding: 5
          }}
          src="https://i.imgur.com/05veXa6.png"
        />
        </div>
         <div style={{display: 'flex', alignItems:'center'}}>
         <Box m="auto"  >
         <Typography variant="h6" component="div" >Cedez Marie Gulane</Typography>
         <Typography variant="h8" component="div" >Fulltime Software Developer, Part-time furmom</Typography>
         </Box>
        </div>
            <Box
              className={classes.button}
            >
                <Link className={classes.button} target="_blank" href="https://www.linkedin.com/in/cedez-marie-gulane-427199110/" ><LinkedInIcon ></LinkedInIcon></Link>
                <Link className={classes.button} target="_blank" href="https://github.com/Sedce/"><GitHubIcon></GitHubIcon></Link>
            </Box>
        </>

    );
};
export default Feed;